p {
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: rgb(69, 6, 96);
    font-weight:400;
    font-size: 22px;
    text-align: justify;
}

.card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 80%;
    border-radius: 15px;
    padding: 10px 16px;
    background: #e0e1e1;
    margin-top: 25px;
}
  
.container {
    display: flex;
    justify-content: center;
    border-radius: 50% solid rgb(4, 4, 4);
    background: transparent;
}

h3 {
    font-weight: 500;
    font-size: 24px;
    text-decoration: none;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: rgb(69, 6, 96);
}
